
.layout.horizontal,
.layout.vertical {
  display: flex;
}
.layout.inline {
  display: inline-flex;
}
.layout.horizontal {
  flex-direction: row;
}
.layout.vertical {
  flex-direction: column;
}
.layout.wrap {
  flex-wrap: wrap;
}
.layout.no-wrap {
  flex-wrap: nowrap;
}
.layout.center,
.layout.center-center {
  align-items: center;
}
.layout.center-justified,
.layout.center-center {
  justify-content: center;
}
.flex {
  flex: 1;
  flex-basis: 0.000000001px;
}
.flex-auto {
  flex: 1 1 auto;
}
.flex-none {
  flex: none;
}




.layout.horizontal-reverse,
.layout.vertical-reverse {
  display: flex;
}
.layout.horizontal-reverse {
  flex-direction: row-reverse;
}
.layout.vertical-reverse {
  flex-direction: column-reverse;
}
.layout.wrap-reverse {
  flex-wrap: wrap-reverse;
}





/**
       * Alignment in cross axis.
       */
.layout.start {
  align-items: flex-start;
}
.layout.center,
.layout.center-center {
  align-items: center;
}
.layout.end {
  align-items: flex-end;
}
.layout.baseline {
  align-items: baseline;
}
/**
 * Alignment in main axis.
 */
.layout.start-justified {
  justify-content: flex-start;
}
.layout.center-justified,
.layout.center-center {
  justify-content: center;
}
.layout.end-justified {
  justify-content: flex-end;
}
.layout.around-justified {
  justify-content: space-around;
}
.layout.justified {
  justify-content: space-between;
}
/**
 * Self alignment.
 */
.self-start {
  align-self: flex-start;
}
.self-center {
  align-self: center;
}
.self-end {
  align-self: flex-end;
}
.self-stretch {
  align-self: stretch;
}
.self-baseline {
  align-self: baseline;
}
/**
 * multi-line alignment in main axis.
 */
.layout.start-aligned {
  align-content: flex-start;
}
.layout.end-aligned {
  align-content: flex-end;
}
.layout.center-aligned {
  align-content: center;
}
.layout.between-aligned {
  align-content: space-between;
}
.layout.around-aligned {
  align-content: space-around;
}




.flex {
  flex: 1;
  flex-basis: 0.000000001px;
}




.block {
  display: block;
}
[hidden], .hidden {
  display: none !important;
}
.invisible {
  visibility: hidden !important;
  pointer-events: none;
}
.relative {
  position: relative;
}
.fit {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
body.fullbleed {
  margin: 0;
  height: 100vh;
}
.scroll {
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}
/* fixed position */
.fixed-bottom,
.fixed-left,
.fixed-right,
.fixed-top {
  position: fixed;
}
.fixed-top {
  top: 0;
  left: 0;
  right: 0;
}
.fixed-right {
  top: 0;
  right: 0;
  bottom: 0;
}
.fixed-bottom {
  right: 0;
  bottom: 0;
  left: 0;
}
.fixed-left {
  top: 0;
  bottom: 0;
  left: 0;
}



.shrinkable {
  min-width: 0;
  min-height: 0;
}

.horizontal-shrinkable {
  min-width: 0;
}

.vertical-shrinkable {
  min-height: 0;
}

.self-no-shrink {
  flex-shrink: 0 !important;
  flex-basis: auto !important; // Otherwise if basis is 0 doesn't make sense
}